import { Component, ContentChildren, EventEmitter, Input, OnInit, Output, QueryList } from '@angular/core';
import { TDSFormField } from 'tds-ui/form-field';
import { TDSSelect } from 'tds-ui/select';
@Component({
  selector: 'tds-header',
  templateUrl: './header.component.html',
  host:{
    class:"flex w-full dark:bg-d-neutral-3-200  items-center justify-between py-1"
  }
})
export class TDSHeaderComponent implements OnInit {
  @Input() urlAvatar:string | null = null;
  @Input() urlLogo:string | null = null;
  @Input() urlLogoText:string | null = null;  
  @Output() readonly onToggle = new EventEmitter<boolean>();
  @Output() readonly clickLogo = new EventEmitter<MouseEvent>();
  @ContentChildren(TDSSelect, { descendants: false }) _selectChildren!: QueryList<TDSSelect>;
  @ContentChildren(TDSFormField, { descendants: false }) _formFieldChildren!: QueryList<TDSFormField>;
  constructor() { }

  ngOnInit(): void {
    
  }
  onClickToggle(e:Event){
    this.onToggle.emit();
  }
  onClickLogo(e:MouseEvent)
  {
    this.clickLogo.emit(e);
  }
}
